.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
}

.card__wrapper {
  flex-direction: column;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img-wrapper {
  justify-content: center;
  align-self: center;
  align-items: center;
  width: 160px;
  height: 134px;
  display: flex;
}

.card__img {
  width: 100%;
  height: 100%;
}

.card__text-wrapper {
  margin-top: 40px;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  margin-top: 4px;
  font-weight: 400;
}

.card__score-wrapper {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 16px;
  display: flex;
}

.card__review {
  color: #060b35;
  font-weight: 400;
}

.card__price-wrapper {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-wrapper {
  justify-content: center;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star-active.edda4c43.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin: 0;
}

.stars__star:last-child {
  background: url("star.822d561e.svg") center no-repeat;
}

html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

body {
  margin: 0;
}

