.stars {
    display: flex;

  &__star {
    width: 16px;
    height: 16px;
    margin-left: 4px;
    background: url(../images/star-active.svg) center no-repeat;
  }

  &__star:first-child {
    margin: 0;
  }

  &__star:last-child {
    background: url(../images/star.svg) center no-repeat;
  }
}

